.main_nav {
  height: 100%;
  padding: 0 1rem;
  display: flex;
  align-items: center;
  background-color: #3b0062;
  margin-bottom: 2.4rem;
}

.logo {
  font-size: 1rem;
  font-weight: bold;
  border: 1px solid white;
  padding: 0.5rem;
  color: white;
}

.spacer {
  flex: 1;
}

.main_nav__items {
  list-style: none;
  padding: 0;
  margin: 0 1.5rem;
  display: none;
}

.navigation_item {
  padding: 0 1.5rem;
  text-decoration: none;
  color: white;
}

.navigation_item:hover {
  color: #fab83f;
  cursor: pointer;
}

@media (min-width: 768px) {
  .main_nav__items {
    display: flex;
  }
}
